<template>
  <div class="images_body">

    <!-- 加载星空 -->
    <stars-show></stars-show>

    <!-- 3D 图片展示 -->
    <carousel-3d
      :autoplay="true"
      :autoplayTimeout="3000"
      :perspective="35"
      :display="displayNumbers"
      :animationSpeed="1000"
      :width="slideSize.width"
      :height="slideSize.height"
      controlsVisible
      :controlsHeight="60"
    >
      <slide v-for="(item, i) in slides" :index="i" :key="i">
        <template slot-scope="obj">
          <img :src="item.src" @click="imgClick(item, obj)" />
        </template>
      </slide>
    </carousel-3d>
  </div>
</template>

<script>
import StarsUtil from './stars.vue'
import utils from "@/utils/common"
import cookie from "@/utils/cookies"
export default {
  name: "DisplayImages",
  components: {
    "stars-show": StarsUtil
  },
  data () {
    return {
      slideSize: {
        width: 600,
        height: 520
      },
      displayNumbers: 5,
      slides: []
    }
  },
  watch: {
    slides: function (val) {
      this.slides = val
    },
    displayNumbers: function (val) {
      this.displayNumbers = val
    },
    slideSize: function (val) {
      this.slideSize = val
    }
  },
  created () {
    
    if (!cookie.getCookie('login_key')) {

      // giuhub pages 页面不支持 router 跳转, 需要使用另一个输入通行码方式
      if (process.env.NODE_ENV === "production") {
        let inputPasscode = prompt("请输入通行码:")
        const { result, message } = utils.validateCode(inputPasscode)
        result && !message ? this.loadImages() : alert(message)
      } else {
        this.$router.push('/login')
      }

    } else {
      this.loadImages()
    }
  },
  methods: {

    loadImages () {
      const shareData = require("@/data/shareData.json")
      this.slides = shareData
      this.displayNumbers = this.slides.length

      if (utils.isMobile()) {
        this.$set(this.slideSize, "width", 400)
        this.$set(this.slideSize, "height", 300)
      }
    },

    imgClick () {
      console.log("点击了图片.")
    }
  }
}
</script>

<style>
html {
  background-image: linear-gradient(45deg, #e3e0e0a1, transparent);
}

.images_body {
  padding: 2rem;
}

.carousel-3d-slide {
  box-shadow: -1px 15px 20px 0px;
  border-color: #00000005;
}

.carousel-3d-controls a span {
  color: #3333334a;
}
</style>




